import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { 
	View, 
	Panel, 
	Div, 
	platform,
	Group,
	Button,
	Cell,
	List,
	Avatar
} from '@vkontakte/vkui';
/* VKIcons */
import Icon24Replay from '@vkontakte/icons/dist/24/replay';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import Icon32Poll from '@vkontakte/icons/dist/32/poll';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24Link from '@vkontakte/icons/dist/24/link';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';

import '@vkontakte/vkui/dist/vkui.css';
import './assets/css/bootstrap.css';
import './assets/css/style.css';

const osname = platform();

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'main',
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Panel id="main">
					<Div>
						<div className="row">
							<div className="col-md-12">
								<div className="text-center text-muted">
									<h1>Твое название</h1>
								</div>
								<div className="text-center buttons">
									<div className="d-flex justify-content-around">
										<div className="justify-content-around">
											<button className="btn btn-rating">
												<Icon32Poll className="buttons_custom" />
											</button>
											<p className="text-buttons text-muted text-monospace">Топ игроков</p>
										</div>
										<div className="justify-content-around">
											<button className="btn btn-exchange">
												<Icon24Filter className="buttons_custom" />
											</button>
											<p className="text-buttons text-muted text-monospace">Дополнительно</p>
										</div>
										<div className="justify-content-around">
											<button className="btn btn-shop">
												<Icon24Flash className="buttons_custom" />
											</button>
											<p className="text-buttons text-muted text-monospace">Магазин</p>
										</div>
									</div>
								</div>
							</div>
							<div className="container">
								<Group>
									<Div>
										<span className="text-muted text-center">Информация о майнинге</span><br />
										<small className="text-muted d-inline-block">Автоматический: 5 коинов / сек</small>
										<small className="text-muted d-inline-block mb-1">За клик: 330 коинов / сек</small>
									</Div>
								</Group>
								<Group title="Информация" description="Онлайн: 100">
									<List>
										<Cell
											before={<Avatar size={48} src="https://sun1-29.userapi.com/c852024/v852024057/fb934/s3GwZhj9xNA.jpg?ava=1" />}
											size="l"
											description="ID: 333333"
											asideContent={<Button level="outline"><Icon24Replay /></Button>}
										>
											Александр Попов
										</Cell>
										<Cell before={<Icon24Mention className="CellIconCustom" />} asideContent={<b>Ваня</b>}>
											Никнейм
										</Cell>
										<Cell before={<Icon24Link className="CellIconCustom" />} asideContent={<b>Пользователь</b>}>
											Привелегия
										</Cell>
										<Cell before={<Icon24Coins className="CellIconCustom" />} asideContent={<b>500kkk</b>}>
											Баланс
										</Cell>
									</List>
								</Group>
								<Group title="Майнить коины">
									<Div>
										<Button size="xl" level="primary">Клик</Button>
									</Div>
								</Group>
							</div>
						</div>
					</Div>
				</Panel>
			</View>
		);
	}
}

export default App;